import React from "react";
import { Row, Col } from "react-bootstrap";
import { useParams } from "react-router-dom";
import AddToCartHook from "../../hook/cart/add-to-cart-hook";
import ViewProductDetailsHook from "../../hook/products/view-product-details-hook";

const ProductsText = () => {
  const { id } = useParams();

  const [item, images, category, brand] = ViewProductDetailsHook(id);

  const [indexColor, colorClick, addToCartHandel] = AddToCartHook(id, item);

  return (
    <div>
      <Row className="mt-2">
        <div className="cat-text">{category ? category.name : null} :</div>
      </Row>
      <Row>
        <Col md="8">
          <div className="cat-title d-inline">
            {item.title}
            <div className="cat-rate d-inline mx-3">{item.ratingsAverage}</div>
          </div>
        </Col>
      </Row>
      <Row>
        <Col md="8" className="mt-4">
          <div className="cat-text d-inline"> :</div>
          <div className="barnd-text d-inline mx-1">{brand.name} </div>
        </Col>
      </Row>
      <Row>
        <Col md="8" className="mt-1 d-flex">
          {item.availableColors
            ? item.availableColors.map((color, index) => {
                return (
                  <div
                    key={index}
                    onClick={() => colorClick(index, color)}
                    className="color ms-2"
                    style={{
                      backgroundColor: color,
                      border: indexColor === index ? "3px solid black" : "none",
                    }}
                  ></div>
                );
              })
            : null}
        </Col>
      </Row>

      <Row>
        <Col md="8" className="mt-3 d-flex">
          <div className="cat-text d-inline">
              : {item.quantity}{" "}
          </div>
        </Col>
      </Row>

      <Row className="mt-4">
        <div className="cat-text"> :</div>
      </Row>
      <Row className="mt-2">
        <Col md="10">
          <div className="product-description d-inline">{item.description}</div>
        </Col>
      </Row>
      <Row className="mt-4">
        <Col md="12">
          {item.priceAfterDiscount >= 1 ? (
            <div className="product-price d-inline px-3 py-3 border">
              <span style={{ textDecorationLine: "line-through" }}>
                {" "}
                {item.price}
              </span>{" "}
              {item.priceAfterDiscount} 
            </div>
          ) : (
            <div className="product-price d-inline px-3 py-3 border">
              <span> {item.price}</span> {" "}
            </div>
          )}
          <div
            onClick={addToCartHandel}
            className="product-cart-add px-3 py-3 d-inline mx-3"
          >
             
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default ProductsText;
