import React from "react";
import { Row } from "react-bootstrap";
import SidebarSearchHook from "../../hook/search/sidebar-search-hook";

const SideFilter = () => {
  const [category, brand, clickCategory, clickBrand, priceFrom, priceTo] =
    SidebarSearchHook();

  let localFrom = localStorage.getItem("PriceForm");
  let localTo = localStorage.getItem("PriceTo");

  return (
    <div className="mt-3">
      <Row>
        <div className="d-flex flex-column mt-2">
          <div className="filter-title"></div>
          <div className="d-flex mt-3">
            <input type="checkbox" value="o" />
            <div className="filter-sub me-2 "></div>
          </div>
          {category ? (
            category.map((item, index) => {
              return (
                <div className="d-flex mt-3" key={index}>
                  <input
                    onChange={clickCategory}
                    type="checkbox"
                    value={item._id}
                  />
                  <div className="filter-sub me-2 ">{item.name}</div>
                </div>
              );
            })
          ) : (
            <h6>  </h6>
          )}
        </div>

        <div className="d-flex flex-column mt-2">
          <div className="filter-title mt-3"></div>
          <div className="d-flex mt-3">
            <input type="checkbox" value="0" />
            <div className="filter-sub me-2 "></div>
          </div>
          {brand ? (
            brand.map((item, index) => {
              return (
                <div className="d-flex mt-3" key={index}>
                  <input
                    onChange={clickBrand}
                    type="checkbox"
                    value={item._id}
                  />
                  <div className="filter-sub me-2 ">{item.name}</div>
                </div>
              );
            })
          ) : (
            <h6>  </h6>
          )}
        </div>

        <div className="filter-title my-3"></div>
        <div className="d-flex">
          <p className="filter-sub my-2">:</p>
          <input
            value={localFrom}
            onChange={priceFrom}
            className="m-2 text-center"
            type="number"
            style={{ width: "50px", height: "25px" }}
          />
        </div>
        <div className="d-flex">
          <p className="filter-sub my-2">:</p>
          <input
            value={localTo}
            onChange={priceTo}
            className="m-2 text-center"
            type="number"
            style={{ width: "50px", height: "25px" }}
          />
        </div>
      </Row>
    </div>
  );
};

export default SideFilter;
