import React, { useState } from "react";
import { Col, Row } from "react-bootstrap";
import { ToastContainer } from "react-toastify";
import OrderPayCashHook from "../../hook/checkout/order-pay-cash-hook";
import OrderPayCardHook from "./../../hook/checkout/order-pay-card-hook";
import ViewAddressesHook from "./../../hook/user/view-addresses-hook";
import GetAllUserCartHook from "../../hook/cart/get-all-user-cart-hook";
import notify from "../../hook/useNotifaction";

const ChoosePayMethoud = () => {
  const [res] = ViewAddressesHook();

  const [addressDetalis, handelChooseAddress, handelCreateOrderCash] =
    OrderPayCashHook();
  const [handelCreateOrderCARD] = OrderPayCardHook(addressDetalis);

  const [, , totalCartPrice, , totalCartPriceAfterDiscount] =
    GetAllUserCartHook();

  const [type, setType] = useState("");
  const changeMathoud = (e) => {
    setType(e.target.value);
  };

  const handelPay = () => {
    if (type === "CARD") {
      console.log("order card");
      handelCreateOrderCARD();
    } else if (type === "CASH") {
      console.log("order cash");
      handelCreateOrderCash();
    } else {
      notify("    ", "warn");
    }
  };

  return (
    <div>
      <div className="admin-content-text pt-5">  </div>
      <div className="user-address-card my-3 px-3">
        <Row className="d-flex justify-content-between ">
          <Col xs="12" className="my-2">
            <input
              onChange={changeMathoud}
              name="group"
              id="group1"
              type="radio"
              value="   "
              className="mt-2 pointer"
            />
            <label className="mx-2 pointer" for="group1">
                  
            </label>
          </Col>
        </Row>

        <Row className="mt-2">
          <Col xs="12" className="d-flex">
            <input
              name="group"
              id="group2"
              type="radio"
              value="  "
              className="mt-2 pointer"
            />
            <label className="mx-2 pointer" for="group2">
                
            </label>
          </Col>
        </Row>

        <Row className="mt-2">
          <Col xs="4" className="d-flex">
            <select
              name="address"
              id="address"
              className="select mt-1 px-2 "
              onChange={handelChooseAddress}
            >
              <option value="0">  </option>
              {res.data ? (
                res.data.map((item, index) => {
                  return (
                    <option key={item._id} value={item._id}>
                      {item.alias}
                    </option>
                  );
                })
              ) : (
                <option key={0} value={0}>
                     
                </option>
              )}
            </select>
          </Col>
        </Row>
      </div>

      <Row>
        <Col xs="12" className="d-flex justify-content-end">
          <div className="product-price d-inline   border">
            {totalCartPriceAfterDiscount >= 1
              ? `${totalCartPrice}  ...   ${totalCartPriceAfterDiscount} `
              : `${totalCartPrice} `}
          </div>
          <div
            onClick={handelPay}
            className="product-cart-add px-3 pt-2 d-inline me-2"
          >
             
          </div>
        </Col>
      </Row>
      <ToastContainer />
    </div>
  );
};

export default ChoosePayMethoud;
