import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, Navigate } from "react-router-dom";
import { createOrderCash } from "../../Redux/action/checkoutAction";
import { getOneUserAddress } from "../../Redux/action/userAddressesAction";
import notify from "../useNotifaction";
import GetAllUserCartHook from "./../cart/get-all-user-cart-hook";

const OrderPayCashHook = () => {
  const [loading, setLoading] = useState(true);
  const [loadingCreate, setLoadingCreate] = useState(true);
  const [addressDetalis, setAddressDetalis] = useState([]);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const [, , , , , cartID] = GetAllUserCartHook();
  console.log(addressDetalis);
  //when change address bu user
  const handelChooseAddress = (e) => {
    setAddressDetalis([]);
    if (e.target.value != "0") get(e.target.value);
  };

  const get = async (id) => {
    setLoading(true);
    await dispatch(getOneUserAddress(id));
    setLoading(false);
  };

  //get address detalis for user
  const resAddress = useSelector(
    (state) => state.userAddressesReducer.oneAddress
  );
  useEffect(() => {
    if (loading === false) {
      if (resAddress && resAddress.status === "success") {
        setAddressDetalis(resAddress.data);
      } else setAddressDetalis([]);
    }
  }, [loading]);

  //when user click
  const handelCreateOrderCash = async () => {
    if (cartID === "0") {
      notify("      ", "warn");
      return;
    }
    if (addressDetalis.length <= 0) {
      notify("    ", "warn");
      return;
    }
    setLoadingCreate(true);
    await dispatch(
      createOrderCash(cartID, {
        shippingAddress: {
          details: addressDetalis.alias,
          phone: addressDetalis.phone,
          city: "",
          postalCode: "",
        },
      })
    );
    setLoadingCreate(false);
  };

  //get response for create orser cash
  const resOrserCash = useSelector(
    (state) => state.checkoutReducer.createOrderCash
  );
  useEffect(() => {
    if (loadingCreate === false) {
      if (resOrserCash && resOrserCash.status === 201) {
        notify("   ", "success");
        setTimeout(() => {
          navigate("/user/all-orders");
        }, 1500);
      } else {
        notify("        ", "warn");
      }
    }
  }, [loadingCreate]);

  return [addressDetalis, handelChooseAddress, handelCreateOrderCash];
};

export default OrderPayCashHook;
