import React, { useState, useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import notify from "../../hook/useNotifaction";
import { createNewUser } from "../../Redux/action/authAction";
import { useNavigate } from "react-router-dom";

const RegisterHook = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [phone, setPhone] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [loading, setloading] = useState(true);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const res = useSelector((state) => state.authReducer.createUser);

  const onChangeName = (e) => {
    setName(e.target.value);
  };

  const onChangeEmail = (e) => {
    setEmail(e.target.value);
  };

  const onChangePhone = (e) => {
    setPhone(e.target.value);
  };

  const onChangePassword = (e) => {
    setPassword(e.target.value);
  };

  const onChangeConfirmPassword = (e) => {
    setConfirmPassword(e.target.value);
  };

  const validationValues = () => {
    if (name === "") {
      notify("    ", "error");
      return;
    }

    if (phone.length <= 10) {
      notify("     ", "error");
      return;
    }

    if (password != confirmPassword) {
      notify("      ", "error");
      return;
    }
  };

  const onSubmit = async () => {
    validationValues();
    setloading(true);
    await dispatch(
      createNewUser({
        name,
        email,
        password,
        passwordConfirm: confirmPassword,
        phone,
      })
    );
    setloading(false);
  };

  useEffect(() => {
    if (loading === false) {
      if (res) {
        if (res.data.token) {
          localStorage.setItem("token", res.data.token);
          notify("   ", "success");
          setTimeout(() => {
            navigate("/login");
          }, 2000);
        }

        if (res.data.errors) {
          if (res.data.errors[0].msg === "E-mail already in use")
            notify("    ", "error");
        }
        if (res.data.errors) {
          if (res.data.errors[0].msg === "accept only egypt phone numbers")
            notify("       11 ", "error");
        }

        if (res.data.errors) {
          if (res.data.errors[0].msg === "must be at least 6 chars")
            notify("      6   ", "error");
        }
      }
    }
  }, [loading]);

  return [
    name,
    email,
    phone,
    password,
    confirmPassword,
    loading,
    onChangeName,
    onChangeEmail,
    onChangePhone,
    onChangePassword,
    onChangeConfirmPassword,
    onSubmit,
  ];
};

export default RegisterHook;
