import React from "react";
import { Col, Row } from "react-bootstrap";
import UserAllOrderCard from "./UserAllOrderCard";

const UserAllOrdersItem = ({ orderItem }) => {
  const formatDate = (dateString) => {
    const options = { year: "numeric", month: "numeric", day: "numeric" };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

  return (
    <div className="user-order mt-2">
      <Row>
        <div className="py-2 order-title">
            #{orderItem.id || 0} ... {" "}
          {formatDate(orderItem.createdAt)}
        </div>
      </Row>
      {orderItem.cartItems
        ? orderItem.cartItems.map((item, index) => {
            return <UserAllOrderCard key={index} item={item} />;
          })
        : null}

      <Row className="d-flex justify-content-between">
        <Col xs="6" className="d-flex">
          <div>
            <div className="d-inline"> </div>
            <div className="d-inline mx-2 stat">
              {orderItem.isDelivered === true ? " " : "  "}
            </div>
          </div>
          <div>
            <div className="d-inline"> </div>
            <div className="d-inline mx-2 stat">
              {orderItem.isPaid === true ? " " : "  "}
            </div>
          </div>

          <div>
            <div className="d-inline"> </div>
            <div className="d-inline mx-2 stat">
              {orderItem.paymentMethodType === "cash"
                ? ""
                : " "}
            </div>
          </div>
        </Col>
        <Col xs="6" className="d-flex justify-content-end">
          <div>
            <div className="barnd-text m-2">
              {orderItem.totalOrderPrice || 0} 
            </div>
          </div>
        </Col>
      </Row>
    </div>
  );
};
export default UserAllOrdersItem;
